import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { Tabs } from 'antd';
import LayoutContentWrapper from '@isomorphic/shared/isomorphic/components/utility/layoutWrapper';
import Profile from './Profile';
import Settings from './Settings';
import Password from './Password';
import SuperFetch from '../../helpers/superFetch';
import moment from 'moment';
function Setting(props) {
  // Declare a new state variable, which we'll call "count"
  const [user, setUser] = useState({});
  const [settings, setSettings] = useState({});
  const [exchangeRateDate, setExchangeRateDate] = useState(null);
  useEffect(() => {
    const fetchData = async () => {
      let userRes = await SuperFetch.get('users/user', {
        idToken: props.idToken,
      });
      let settingsRes = await SuperFetch.get('users/settings');
      let exchangeRateDateRes = await SuperFetch.get('users/exchangeRateDate');
      setUser(userRes.data);
      setSettings(settingsRes.data);
      setExchangeRateDate(
        moment(exchangeRateDateRes.data.date).format('DD MMM YYYY')
      );
    };
    fetchData();
  }, [props.idToken]);
  const callback = e => {
    //console.log(e)
  };
  return (
    <LayoutContentWrapper style={{ height: '100vh' }}>
      <Tabs
        defaultActiveKey="1"
        onChange={callback}
        tabPosition={'left'}
        type="card"
        style={{ height: '100vh' }}
      >
        <Tabs.TabPane tab="Profile" key="1">
          <Profile user={user} />
        </Tabs.TabPane>
        <Tabs.TabPane tab="Password" key="2">
          <Password user={user} />
        </Tabs.TabPane>
        <Tabs.TabPane tab="Public Settings" key="3">
          <Settings settings={settings} exchangeRateDate={exchangeRateDate} />
        </Tabs.TabPane>
        <Tabs.TabPane tab="Others" key="4">
          Others
        </Tabs.TabPane>
      </Tabs>
    </LayoutContentWrapper>
  );
}

export default connect(state => {
  return { idToken: state.Auth.idToken };
}, {})(Setting);
