import React from 'react';
import { Input, Row, Col } from 'antd';
import Button from '@isomorphic/shared/isomorphic/components/uielements/button';
import Form from '@isomorphic/shared/isomorphic/components/uielements/form';
import SuperFetch from '../../../helpers/superFetch';
import notification from '@isomorphic/shared/isomorphic/components/Notification';
const FormItem = Form.Item;
function Settings(props) {
  // Declare a new state variable, which we'll call "count"
  const { getFieldDecorator } = props.form;
  const handleSave = e => {
    e.preventDefault();

    props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        SuperFetch.put('users/settings', {
          name: 'accountantEmail',
          value: values.email,
        }).then(response => {
          if (response.status === 200) {
            notification('success', 'Setting Updated');
          } else {
            notification('error', 'something goes wrong');
          }
        });
      }
    });
  };
  console.log(props.exchangeRateDate);
  return (
    <Row type="flex" justify="center">
      <Col span={12}>
        <Form className="isoSignUpForm" onSubmit={handleSave}>
          <FormItem label={<span>Accountant Email:</span>}>
            {getFieldDecorator('email', {
              rules: [
                {
                  type: 'email',
                  message: 'The input is not valid E-mail!',
                },
                {
                  required: true,
                  message: 'Please input your User Email!',
                },
              ],
            })(
              <Input
                id="inputUserName"
                name="inputUserName"
                size="large"
                placeholder="Accountant Email"
              />
            )}
          </FormItem>
          <FormItem label={<span>ExchangeRate Update Date:</span>}>
            <span>{props.exchangeRateDate}</span>
          </FormItem>
          <Button type="primary" htmlType="submit">
            SAVE
          </Button>
        </Form>
      </Col>
    </Row>
  );
}

const WrappedSettings = Form.create({
  mapPropsToFields(props) {
    console.log(
      props.settings.find(item => item.name === 'accountantEmail').value
    );
    return {
      email: Form.createFormField({
        ...props.email,
        value: props.settings.find(item => item.name === 'accountantEmail')
          .value,
      }),
    };
  },
})(Settings);

export default WrappedSettings;
