import React, { useState } from 'react';
import { connect } from 'react-redux';
import { Input, Row, Col } from 'antd';
import Button from '@isomorphic/shared/isomorphic/components/uielements/button';
import Form from '@isomorphic/shared/isomorphic/components/uielements/form';
import SuperFetch from '../../../helpers/superFetch';
import notification from '@isomorphic/shared/isomorphic/components/Notification';
const FormItem = Form.Item;
function Profile(props) {
  // Declare a new state variable, which we'll call "count"
  const { getFieldDecorator } = props.form;
  const [isVerified, setIsVerified] = useState(false);
  const handleSave = e => {
    e.preventDefault();
    props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        if (isVerified) {
          SuperFetch.put('users/user', {
            id: props.user.id,
            newPassword: values.newPassword,
          }).then(response => {
            if (response.status === 200) {
              notification('success', 'Password Updated');
            } else {
              notification('error', 'something goes wrong');
            }
          });
        } else {
          SuperFetch.post('login', {
            username: props.user.userName,
            ...values,
          }).then(response => {
            console.log(response.data);
            if (response.data.error)
              props.form.setFields({
                password: { errors: [new Error(response.data.error)] },
              });
            else if (response.data.token) {
              setIsVerified(true);
            } else {
              notification('error', 'something goes wrong');
            }
          });
        }
      }
    });
  };
  const validatePassword = (rule, value, callback) => {
    callback();
  };
  return (
    <Row type="flex" justify="center">
      <Col span={12}>
        <Form className="isoSignUpForm" onSubmit={handleSave}>
          <FormItem hasFeedback label={<span>Old password:</span>}>
            {getFieldDecorator('password', {
              rules: [
                {
                  required: true,
                  message: 'Please input your password!',
                },
                {
                  validator: validatePassword,
                },
              ],
            })(
              <Input.Password
                id="password"
                name="password"
                size="large"
                placeholder="Password"
              />
            )}
          </FormItem>
          {isVerified ? (
            <FormItem hasFeedback label={<span>Old password:</span>}>
              {getFieldDecorator('newPassword', {
                rules: [
                  {
                    required: true,
                    message: 'Please input your password!',
                  },
                ],
              })(
                <Input.Password
                  id="newPassword"
                  name="password"
                  size="large"
                  placeholder="Password"
                />
              )}
            </FormItem>
          ) : null}
          <Button type="primary" htmlType="submit">
            {isVerified ? 'Update' : 'Verify'}
          </Button>
        </Form>
      </Col>
    </Row>
  );
}

const WrappedProfile = Form.create()(Profile);

export default connect(
  state => ({
    isLoggedIn: state.Auth.idToken !== null ? true : false,
  }),
  {}
)(WrappedProfile);
