import React from 'react';
import { Input, Row, Col } from 'antd';
import Button from '@isomorphic/shared/isomorphic/components/uielements/button';
import Form from '@isomorphic/shared/isomorphic/components/uielements/form';
import SuperFetch from '../../../helpers/superFetch';
const FormItem = Form.Item;
function Profile(props) {
  // Declare a new state variable, which we'll call "count"
  const { getFieldDecorator } = props.form;
  const handleSave = e => {
    e.preventDefault();

    props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        SuperFetch.put('users/user', { id: props.user.id, ...values }).then(
          response => {
            console.log(response);
          }
        );
      }
    });
  };
  return (
    <Row type="flex" justify="center">
      <Col span={12}>
        <Form className="isoSignUpForm" onSubmit={handleSave}>
          <Row type="flex" justify="space-between">
            <Col span={11}>
              <FormItem label={<span>First Name:</span>}>
                {getFieldDecorator('firstName', {
                  rules: [
                    {
                      required: true,
                      message: 'Please input your First Name!',
                    },
                  ],
                })(
                  <Input
                    id="inputFirstName"
                    name="inputFirstName"
                    size="large"
                    placeholder="First name"
                    style={{ marginRight: 0 }}
                  />
                )}
              </FormItem>
            </Col>
            <Col span={11}>
              <FormItem label={<span>Last Name:</span>}>
                {getFieldDecorator('lastName', {
                  rules: [
                    {
                      required: true,
                      message: 'Please input your Last Name!',
                    },
                  ],
                })(
                  <Input
                    id="inputLastName"
                    name="inputLastName"
                    size="large"
                    placeholder="Last name"
                    style={{ marginRight: 0 }}
                  />
                )}
              </FormItem>
            </Col>
          </Row>

          <FormItem label={<span>User Name:</span>}>
            {getFieldDecorator('userName', {
              rules: [
                {
                  required: true,
                  message: 'Please input your User Name!',
                },
              ],
            })(
              <Input
                id="inputUserName"
                name="inputUserName"
                size="large"
                placeholder="User name"
              />
            )}
          </FormItem>
          <FormItem label={<span>Email:</span>}>
            {getFieldDecorator('email', {
              rules: [
                {
                  type: 'email',
                  message: 'The input is not valid E-mail!',
                },
                {
                  required: true,
                  message: 'Please input your User Email!',
                },
              ],
            })(
              <Input id="email" name="email" size="large" placeholder="Email" />
            )}
          </FormItem>
          <Button type="primary" htmlType="submit">
            SAVE
          </Button>
        </Form>
      </Col>
    </Row>
  );
}

const WrappedProfile = Form.create({
  mapPropsToFields(props) {
    return {
      firstName: Form.createFormField({
        ...props.firstName,
        value: props.user.firstName,
      }),
      lastName: Form.createFormField({
        ...props.lastName,
        value: props.user.lastName,
      }),
      userName: Form.createFormField({
        ...props.userName,
        value: props.user.userName,
      }),
      email: Form.createFormField({
        ...props.email,
        value: props.user.email,
      }),
    };
  },
})(Profile);

export default WrappedProfile;
